import { createRouter, createWebHistory } from 'vue-router';
import ReportListPage from './components/ReportListPage.vue';  // ホームページ
import DetailsPage from './components/DetailsPage.vue';  // アイテム詳細ページ

const routes = [
  { path: '/', component: ReportListPage, props: true },
  { path: '/details/:id', component: DetailsPage, props: true },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
