<template>
    <h5>{{ id }}</h5>
    <!-- 詳細表示に必要なコンテンツを追加 -->
    <q-page>
      <div>
        <form @submit.prevent="handleSubmit">
          <div>
            <label for="reportDate">日付:</label>
            <input type="text" id="reportDate" v-model="reportDate" />
          </div>
          <div>
            <label for="startTime">開始時刻:</label>
            <input type="time" id="startTime" v-model="startTime" />
          </div>
          <div>
            <label for="endTime">終了時刻:</label>
            <input type="time" id="endTime" v-model="endTime" />
          </div>
          <div>
            <label for="breakTime">休憩時間:</label>
            <input type="text" id="breakTime" v-model="breakTime" />
          </div>
          <div>
            <label for="detail">業務内容:</label>
            <input type="text" id="detail" v-model="detail" />
          </div>
          <div>
            <button type="submit">送信</button>
          </div>
        </form>

        <p v-if="submitted">送信されたユーザー名: {{ username }}</p>
      </div>
    </q-page>
  </template>
  
  <script>
  
  export default {
    data() {
      return {
        reportDate: '',  // 入力値を格納する
        submitted: false,  // フォームが送信されたかどうか
      };
    },
    methods: {
      handleSubmit() {
        // フォーム送信時の処理
        this.submitted = true;
        console.log('送信されたユーザー名:', this.username);
      },
    },
  };
  </script>
  
  <script setup>
  import { useRoute } from 'vue-router';
    
  const route = useRoute();
  const id = route.params.id;
  </script>
  
  <style>
  </style>